const
domIsReady  = require('core/browser/dom-ready'),
CoreFactory = require('core/browser/factory')

class Application
{
  constructor(components)
  {
    const coreFactory = new CoreFactory()
    this.core         = coreFactory.create(components)

    domIsReady((components) =>
    {
      this.core.load().then(() =>
      {
        this.core.locate('core/bootstrap').bootstrap().then(() =>
        {
          const bus = this.core.locator.locate('core/bus')
          bus.emit({
            channelId : 'domain-events',
            name      : 'app-initialized',
            data      : {
              components,
              total : components.length
            }
          })
        })
      })
    })
  }
}

module.exports = Application
